import React, { Component } from 'react'

import TextInput from 'components/common/TextInput'
import Button from 'components/common/Button'

import facebook from './assets/facebook.svg'
import twitter from './assets/twitter.svg'
import slack from './assets/slack.svg'

import './index.scss'
import PropTypes from "prop-types";

const validator = email => {
  let re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return re.test(email)
}

class Form extends Component {
  static propTypes = {}

  state = {
    name: '',
    email: '',
    message: ''
  }

  postMessageHandler = () => {
    console.log('Merge?')
    console.log(this)
  }

  onChangeName = e => {
      const { value } = e.target
      this.setState({ name: value })
  }

  onChangeEmail = e => {
      const { value } = e.target
      this.setState({ email: value })
  }

  onChangeMessage = e => {
      const { value } = e.target
      this.setState({ message: value })
  }

  render () {
    console.log(this.state)
    return (
      <div className='Form'>
        <p>
          Fiind o comunitate de oameni entuziasmai de tehnologie si programare,
          sediul nostru se afla in cadrul Facultii de Automatic si
          Calculatoare, din cadrul Universitati Politehnica Bucureti. Aici
          desfsurm i majoritatea proiectelor la care eti binevenit s
          participi sau s ne ajuti sa le organizam. Pentru a putea lua legatura
          cu tine, te rugm s ne trimii un mesaj, completnd formularul de mai
          jos, urmnd ca noi sa luam legatura cu tine in cel mai scurt timp
          posibil.
          <br />
          <br />
          Abia asteptam sa colaboram impreuma.
        </p>
        <div className='social'>
          <span>
            <img src={twitter} />
            @rosedu
          </span>
          <span>
            <img src={facebook} />
            /rosedu
          </span>
          <span>
            <img src={slack} />
            #rosedu
          </span>
        </div>
        <div className='form'>
          <TextInput id={'name_input'} label='Nume'  value={this.state.name} onChange={this.onChangeName}/>
          <TextInput id={'email_input'} label='E-mail' validator={validator} value={this.state.email} onChange={this.onChangeEmail} />
          <TextInput id={'mesaj_input'} label='Mesaj' value={this.state.message} value={this.state.message} onChange={this.onChangeMessage}/>
          <Button onClick={this.postMessageHandler}> Trimite mesaj </Button>
        </div>
      </div>
    )
  }
}

export default Form



