import React, { Component } from 'react'

import Title from 'components/common/Title'
import ButtonLink from 'components/common/ButtonLink'

import './index.scss'

class Sponsorship extends Component {
  static propTypes = {}
  static defaultProps = {}

  render () {
    return (
      <div className='Sponsorship'>
        <Title text='Susine ROSEdu' color='white' />
        <div className='content'>
          <p className='content_text'>
            Dac doreti s susii activitile organizaiei ROSEdu, avem aici
            mai multe variante prin care poi face asta. Daca ai nevoie de
            ajutor, sau eti interesat de un parteneriat, folosete formularul
            de contact i hai s ne auzim!
          </p>
        </div>
        <div className='boxes'>
          <div className='box'>
            <h2>Voluntariat</h2>
            <p>Vrei s fii un voluntar ROSEdu ?</p>
            <ButtonLink primary={true} target={'#contact'}>
              Trimite mesaj
            </ButtonLink>
          </div>
          <div className='box'>
            <h2>Cont bancar</h2>
            <p>
              Ne poi susine i prin donarea unei sume de bani la urmtorul
              cont:
            </p>
            <h3>RO99INGB12345678</h3>
          </div>
          <div className='box'>
            <h2>Sponsorizare</h2>
            <p>
              Dac doreti s avem un parteneriat direct de sponsorizare,
              trimite-ne un mesaj!
            </p>
            <ButtonLink primary={true} target={'#contact'}>
              Trimite mesaj
            </ButtonLink>
          </div>
        </div>
      </div>
    )
  }
}

export default Sponsorship



