import React, { Component } from 'react'

import ButtonLink from 'components/common/ButtonLink'

import facebook from './assets/facebook.png'
import headerImage from './assets/logo_principal.jpg'
import roseduLogo from './assets/logoWhite.svg'
import slack from './assets/slack.svg'
import mail from './assets/mail.svg'

import './index.scss'

class Header extends Component {
  static propTypes = {}
  static defaultProps = {}

  render () {
    return (
      <div className='Header'>
        <header>
          <img src={roseduLogo} alt='' />
          <nav>
            <a href='#about'>
              <span>despre noi</span>
            </a>
            <a href='#projects'>
              <span>proiecte</span>
            </a>
            <a href='#contact'>
              <span>contact</span>
            </a>
            <a href='https://rosedu.herokuapp.com/' target='_blank'>
              <img src={slack} alt='' />
            </a>
            <a href='https://www.facebook.com/rosedu.org' target='_blank'>
              <img src={facebook} alt='' />
            </a>
            <a
              href='http://lists.rosedu.org/listinfo/rosedu-general'
              target='_blank'
            >
              <img src={mail} alt='' />
            </a>
          </nav>
          <ButtonLink primary target={'#sponsorship'}>
            Susine ROSEdu!
          </ButtonLink>
        </header>
        <main>
          <div className='text'>
            <h2>Susinem o educaie</h2>
            <h1>open source</h1>

            <p>
              ROSEdu este o organizaie Open Source bazate pe o comunitate
              format din programri i entuziai de software. Misiunea pentru
              care suntem foarte dedidacai este aceea de a iniia, sprijinii i
              dezvolta educaia bazat pe valorile Open Source.
            </p>

            <ButtonLink target={'#projects'}>
              {' '}
              Vezi proiectele noastre{' '}
            </ButtonLink>
          </div>
          <img className='header_logo' src={headerImage} alt='' />
        </main>
      </div>
    )
  }
}

export default Header



