import React, { Component } from 'react'
import PropTypes from 'prop-types'

import Title from 'components/common/Title'
import Person from 'components/common/Person'

import './index.scss'

/**
 *  About section component
 */
class About extends Component {
  static propTypes = {
    people: PropTypes.arrayOf(PropTypes.object)
  }
  static defaultProps = {
    people: []
  }

  get people () {
    return this.props.people.map((x, i) => <Person key={i} {...x} />)
  }

  render () {
    return (
      <div className='About'>
        <Title text='despre' bolded='noi' color='#ef4924' />
        <div className='content'>
          <div className='text'>
            <p className='about_text'>
              ROSEdu crede n importana educaiei si a procesului de nvare.
              Credem c, prin comunicare i colaborare, putem nva unul de la
              cellalt sa dezvoltm lucruri noi i a contribuim la proiecte
              existente. Open Source este pentru noi, nsui mediul in care
              suntem activi, formand o retea de oameni deschisi, proiecte ci
              activitai lipite impreuna de technologie si internet, ceea ce ne
              permite cooperarea si schimbul cosntant de idei.
            </p>
            <p className='about_text'>
              Majoritatea membrilor Rosedu sunt in prezent studenti sau
              absolventi ai Universitatii Politehnica din Bucureti, dar noi
              suntem intotdeauna bucuroi de noi memberi, indfiderent de unde
              provin. Te asteptam i pe tine!
            </p>
          </div>
          <div className='people'>{this.people}</div>
        </div>
      </div>
    )
  }
}

export default About



