import React, { Component } from 'react'
import PropTypes from 'prop-types'

import './index.scss'

import Title from 'components/common/Title'
import Button from 'components/common/Button'
import ButtonLink from 'components/common/ButtonLink'
import Project from 'components/common/Project'

/**
 * Projects section component
 */
class Projects extends Component {
  static propTypes = {
    projects: PropTypes.arrayOf(PropTypes.object)
  }
  static defaultProps = {
    projects: []
  }

  render () {
    const {
      props: { projects }
    } = this

    return (
      <div className='Projects'>
        <Title text='proiecte' color='#fff' />

        <div className='content'>
          <div className='text'>
            <div className='text_content'>
              <p>
                Fiind o comunitate de oameni carora le place sa stie cat mai
                multe, proiectele noastre acopera multe arii din domeniul IT.
                Daca doresti sa participi la proiectele noastre te asteptam sa
                ne contactezi si sa ne cunoastem.
              </p>
              <p>
                In cazul in care i-ar plcea mai mult s ajui la partea
                organizatoric i ai experiena cu lucruri precum Linux,
                programare funcional, programare orientat pe obiecte,
                proiecte Open Source i eti dornic s o trimii mai departe, te
                ateptm s ne ajui s mbuntim mpreun educaia.
              </p>
            </div>
            <ButtonLink target={'#contact'}>Vrei s te implici ?</ButtonLink>
          </div>
          <div className='list' style={{ marginLeft: '70px' }}>
            {projects.map((x, i) => <Project key={i} {...x} />)}
          </div>
        </div>
      </div>
    )
  }
}

export default Projects



